
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import './styles.css';

class TextInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      label: '',
      value: '',
      disabled: false,
    };
  }

  componentWillMount() {
    this.setState({
      label: this.props.label, value: this.props.value, disabled: this.props.disabled, pattern: this.props.pattern, type: this.props.type, required: this.props.required, className: this.props.className, minLength: this.props.minLength, maxLength: this.props.maxLength,
    });
  }

  componentWillReceiveProps(np) {
    this.setState({ label: np.label, value: np.value, disabled: np.disabled });
  }

  render() {
    const {
      label, value, disabled, pattern, type, required, className,
    } = this.state;
    return (
      <div className={`input-label-div ${className}`} style={this.props.style}>
        <input
          type={type || 'text'}
          name={label}
          className="inp "
          disabled={disabled}
          value={value}
          onChange={(e) => this.props.onTextChange(e.target.value)}
          required={required}
          pattern={pattern || null}
          placeholder=" "

        />
        <label className="inp-label" name={label}  >
          {label}
        </label>
      </div>

    );
  }
}

const mapStateToProps = (state) => ({
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(TextInput);
